import React, { Component } from "react";
import "./UpDownButton.css";

export default class UpDownButton extends Component {
  constructor(props) {
    super(props);
    this.state = { scrollPose: 0 };
  }
  componentDidMount() {
    setInterval(() => this.setState({ scrollPose: window.pageYOffset }), 10);
  }
  componentWillUnmount() {
    clearInterval(this.interval);
  }
  render() {
    return (
      <div>
        <a
          className="goTo goToFirst"
          href={this.state.scrollPose >= 1300 ? "#about" : "#profile"}
        >
          <i className="fas fa-chevron-up"></i>
        </a>
        <a
          className="goTo goToLast"
          href={this.state.scrollPose <= 400 ? "#about" : "#github"}
        >
          <i className="fas fa-chevron-down"></i>
        </a>
      </div>
    );
  }
}
